@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arvo&display=swap);
body {
  margin: 0;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: default;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
/*logo*/


.home{
    height: 100vh;
    width: 100vw;
    background-image: url(/memorygame/static/media/hero_main.5ce25af3.png);
    background-size: cover;
}
.home-container{
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.351);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header{
    width: 100vw;
    height: 13vh;
    display: flex;
    align-items: center;
    padding-left: 2vh;
}
.header-game{
    background-color: #7495e1;
    color: white;
    justify-content: space-between;
}
.header-list-link{
    text-decoration: none;
    color: white;
    transition: all 0.3s ease-out; 
}
.header-list-item:hover .header-list-link{
    color: black;
}
.logo{
    width: 30vw;
    display: flex;
    align-items: center;
    font-size: 2.2em;
    font-family: 'Arvo';
    color: white;
    border-radius: 20px;
    padding: 5px;
    transition: all 0.3s ease-out; 
}

ul{
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    color: white;
    font-size: 1.7em;
    font-family: 'Montserrat',sans-serif;
}
.header-list-item{
    padding: 10px;
    cursor: pointer;
    border: 4px solid transparent ;
}
.header-list-item:hover{
    transition: all 0.2s;
    color: black;
    border-bottom: 4px solid yellowgreen;
    background-color: white;
}

.start{
    position: absolute;
    bottom: 15vh;
    font-size: 2.5em;
    cursor: pointer;
    font-family: 'Montserrat';
    background-color: black;
    color: white;
    border: none;
    border-radius: 20px;
    padding: 20px;
    transition: all 0.3s ease-in-out;
}
.abs-text{
    position: absolute;
    top: 35vh;
    font-size: 4em;
    font-family: 'Arvo';
    color: #e1dfdf;
}
.start:hover{
    color: black;
    background-color: white;
}

.curtain{
    position: absolute;
    transition: all 1s ease-in-out;
    height: 100vh;
    width: 100vw;
    background-color: white;
    z-index: 100;
}
.open{
    top: 0;
}
.close{
    top: 100%;
}

/*game header*/

.menu{
    position: absolute;
    top: 0;
    left: -100%;
    width: 25vw;
    height: 100vh;
    background-color: black;
    padding: 20px;
    transition: all 0.5s ease-out;
}
.menu-button{
    cursor: pointer;
    font-size: 1.5em;
    margin-right: 20px;
    transition: all 0.2s ease-in-out;
}
.menu-button:hover{
    color: yellowgreen;
}
.menu-open{
    left: 0;
}
.side-menu{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
    justify-content: space-evenly;
    grid-gap: 50px;
    gap: 50px;
    margin-top: 20%;
    overflow: scroll;
}
.close-menu{
    position: absolute;
    font-family: 'Montserrat';
    top: 20px;
    right: 4%;
    font-size: 2.5em;
    color: white;
    cursor: pointer;
    transition: all 0.3s ease-out;
}


.scoreboard{
    width: 40vw;
    margin-right: 15vw;
}
.scores-container{
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}
.score-text{
    position: absolute;
    top: 1%;
    left: 65%;
    font-family: 'arvo';
    font-size: 1.5em;
    color: yellow;
}
.score{
    font-family: 'arvo',sans-serif;
    font-size: 1.3em;
    color: white;
}

.close-menu:hover{
    color: red;
}

/* grid container */
.grid-container{
    overflow: scroll;
    width: 100vw;
    height: 86vh;
    display: grid;
    grid-template-columns: repeat(4,15%);
    grid-template-rows: repeat(4,50%);
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
    padding-top: 5vh;
}
.character-card{
    border: 2px solid gray;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.3s;
    padding: 5px;
    text-align: center;
    font-family: 'Montserrat';
    text-transform: capitalize;
    grid-gap: 10px;
    gap: 10px;
    font-size: 1.7em;
}
.character-card:hover{
    box-shadow: 0 0 20px lightgray;
}

.char-image{
    height: 70%;
    width: 100%;
    object-fit: cover;
}

.rules{
    overflow: scroll;
    height: 35vh;
}
.rules-title{
    font-size: 1.3em;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    color: red;
    padding: 10px;
    overflow: scroll;
}
.rules-text{
    font-size: 0.6em;
    color: white;
}

/* WebKit and Chromiums scrollbars*/
::-webkit-scrollbar {
    width: 4px;
    background-color: transparent;
  }
  
  ::-webkit-scrollbar-thumb {
    background: red;
    border-radius: 5px;
  }

  ::-webkit-scrollbar-corner {
    background: transparent;
  }
